
@mixin splash_area_title_common($desktop_width: 50%) {
  width: 100%;
  height: 100%;
  justify-content: center;
  z-index: 1;
  padding: 1.5rem; // @todo: should be $mobile_edge_padding_factor

  @include bp($stack) {
    position: absolute;
    top: 0;
    height: 100%;
    width: $desktop_width;
    display: flex;
    flex-direction: column;
    flex: 1;
    justify-content: flex-start;
    background-color: $black;
    border-left: $borderWidth solid $white;
    border-right: $borderWidth solid $white; } }


@mixin splash_area_title_right($desktop_width: 33%) {
  @include splash_area_title_common($desktop_width);

  @include bp($stack) {
    padding: rv(2);
    right: 0; }

  @include bp($desktop) {
 } }    //padding: $title_area_outside_padding $title_area_outside_padding $title_area_bottom_padding $title_area_inside_padding


@mixin splash_area_title_left($desktop_width: 33%) {
  @include splash_area_title_common($desktop_width);

  @include bp($stack) {
    padding: rv(2);
    left: 0; }

  @include bp($desktop) {
    padding: $title_area_outside_padding $title_area_inside_padding $title_area_bottom_padding $title_area_outside_padding; } }

