// @todo:  There are some present factors in 00_configuration_layout like $mobile_edge_padding_factor and $edge_padding_factor that should be used here.
section {
  main {
    > div {
      > * {
        margin-left: rv(0);
        margin-right: rv(0);
        @include bp($stack) {
          margin-left: auto;
          margin-right: auto; }
        &:first-child {
          margin-top: rv(3)!important; } }
      > .storymap {
          margin-left: auto;
          margin-right: auto;
          margin-top: 0!important; }
      > .tl-timeline {
          max-width: 100%!important;
          margin-left: auto;
          margin-right: auto;
          margin-top: 0!important; } } }
  > div[class*="block-"] {
      margin-top: rv(4); }

  > div {
    > div {
      > div {
        margin-left: rv(0);
        margin-right: rv(0);

        @include bp($stack) {
          margin-left: auto;
          margin-right: auto; } } } } }

main, #feature-tier-1 {
  > div {
    margin-bottom: 0;
    > * {
      @include inset-text(); }
    > .storymap {
      @include inset-full(); }
    > .slideshow {
      @include negative-content-margin(4); }
    > .tl-timeline {
      max-width: 100%!important;
      @include inset-full(); }
    > #feature-1 {
      @include negative-content-margin(6); }
    > .browse-controls {
      @include negative-content-margin(6); }
    > .search-filters {
      @include negative-content-margin(6); }
    > .poster {
      @include negative-content-margin(8); }
    > .opaque {
      @include negative-content-margin(6); }

    > .hu-outset-margin {
      @include negative-content-margin(6); }
    > .explore-the-archive {
      @include negative-content-margin(6);
      > div {
        @include inset-text(); } }
    > .resource-list {
      @include negative-content-margin(6); }

    .slideshow-block, .slideshow-with-audio {
      @include negative-content-margin(7); }

    > .file {
      @include negative-content-margin(4);
      .media-render {
        &:before {
          content: "";
          height: rv(-2);
          width: 100%;
          background: $black;
          display: block;
          margin-bottom: rv(-5); } }

      .caption {
        text-align: center;
        margin-top: rv(-5);
        padding-top: rv(-1);
        padding-bottom: rv(-2);
        color: $white;
        background: $black;
        border-bottom-right-radius: $borderRadius; }
      h3 {
        display: none; } }

    > ul {
      display: grid;
      grid-template-columns: 1fr;
      grid-column-gap: rv(3);
      grid-row-gap: rv(-2);
      grid-auto-columns: min-content;
      padding: 0;
      list-style-type: none;


      li {
        position: relative;
        left: rv(0);
        @include bp($stack) {
          left: rv(3); }
        h5 {
          font-family: $basefont;
          margin-top: 0;
          margin-bottom: rv(-3); }

        &:before {
          content: "";
          color: $black;
          font-weight: bold;
          display: inline-block;
          width: rv(-2);
          position: absolute;
          top: 14px;
          left: rv(1) * -1;
          font-size: rv(0);
          background: url("../../asset/img/svg/icons/list_line.svg") no-repeat;
          height: rv(-2); } } } }


  .media-render {
    a {
      img {
        filter: grayscale(100%); } } }
  blockquote {
    background: $bgcolor2;
    color: $clr4;
    padding: rv(1) rv(3)!important;
    font-family: $codefont;
    padding-bottom: rv(4)!important;
    margin-top: rv(4)!important;
    margin-bottom: rv(4)!important;
    @include tilt;
    &:after {
      content: " ";
      width: rv(1);
      height: rv(1);
      background: url("../../asset/img/svg/icons/quotes_icon.svg") no-repeat;
      position: absolute;
      right: rv(-1);
      top: rv(-1) * -1;
      border: 1px solid;
      padding: rv(-1);
      background-size: rv(0);
      background-position: center;
      border-radius: 100%;
      @include bp($stack) {
        content: " ";
        width: rv(1);
        height: rv(1);
        background: url("../../asset/img/svg/icons/quotes_icon.svg") no-repeat;
        padding: rv(1);
        background-size: rv(2);
        position: relative;
        float: right;
        border-radius: 100%;
        background-position: center;
        border: 1px solid;
        top: unset; } }




    pre {
      font-family: $codefont;
      @include dashed-line;
      display: inline-block;
      padding-top: rv(-3);
      float: left; }
    p {}
    th, td {}
    text-align: left;
    border-spacing: 0;
    padding: rv(-5);
    font-size: font-value(-1);
    @include bp($stack) {
      padding: rv(-3);
      font-size: font-value(0); }
    * {
      @include untilt; } }


  th, td {
    text-align: left;
    border-spacing: 0;
    padding: rv(-5);
    font-size: font-value(-2);
    font-family: $basefont;
    @include bp($stack) {
      padding: rv(-3);
      font-size: font-value(-1); } }
  th {
    background: $clr5;
    color: $white; }
  tr:nth-child(even) {
    > td {
      background: neutral(10);
      border-top: 5px solid $white; } }
  tr {
    > td {
      background: neutral(5);
      border-top: 5px solid white; } }

  table {
    border-spacing: 0;
    @include bp($stack) {
      width: 100%; } } }

.huronia-identity {     // marked for deletion
  background-color: $black;
  height: rv(1);
  width: 60%;
  display: block;
  margin: rv(-1);

  @include bp($stack) {
    background-color: $black;
    height: 100%;
    width: 330px;
    margin: 0; }

  &#logo-mobile {
    @include bp($stack) {
      display: none; }

    img {
      height: 100%;
      width: auto; } }

  &#logo-stack {
    display: none;
    @include bp($stack) {
      display: flex;
      flex-direction: row;
      align-items: center; }

    img {
      width: 100%;
      transform: translateY(-6.5px); } } } // Fudge factor to bring the logo baseline in line with menu text baseline


.history-identity {
  background-color: $black;
  display: block;
  font-family: BentonSans;
  font-weight: 900;
  padding: rv(-1);
  font-size: font-value(-1);
  min-width: 235px;
  max-width: 300px;

  @include bp($stack) {
    background-color: $black;
    height: 100%;
    width: 100%;
    margin: 0;
    font-size: inherit;
    min-width: 100%;
    max-width: 100%;

    img {
      height: 100%;
      width: auto; } }

  &#logo-stack {
    @include bp($stack) {
      display: flex;
      flex-direction: row;
      align-items: center; }

    img {
      width: rv(2);
      padding-right:  rv(-1); } } }

.site-page-pagination {
  display: none; }

body {
  > header {
    align-items: center;
    display: grid;
    grid-template-columns: 3fr 1fr;
    border-bottom: $borderWidth solid $white;
    overflow: hidden;
    background: $black;

    @include bp($stack) {
      display: grid;
      grid-template-rows: initial;
      grid-template-columns: 2fr 3fr;
      grid-row-gap: initial; }

    .access-button {
      &.up-arrow-toggle {
        .down-arrow-black {
          transform: rotate(180deg);
          position: relative;
          top: -4px; } }
      display: none;
      &:hover {
        color: $white; }
      @include bp($stack) {
        background: $clr1;
        padding: 0 rv(-3);
        color: $black;
        white-space: nowrap;
        display: flex;
        align-items: center;
        text-align: center;
        height: 100%;
        border-left: rv(-10) solid $white; } }
    .menu-access-button {
      &.up-arrow-toggle {
        .down-arrow-black {
          transform: rotate(180deg);
          position: relative;
          top: -4px; } }
      padding: 0 rv(-3);
      color: $black;
      white-space: nowrap;
      display: flex;
      align-items: center;
      text-align: left;
      height: 100%;
      line-height: rv(-1);
      &:hover {
        color: $white; }
      @include bp($stack) {
        display:none!important {} } }

    img {
      width: rv(12);
      float: left; }
    nav {
      background: $black;
      height: 100%;
      display: none;
      @include bp($stack) {
        display: block;
        text-align: right;
        padding: 10px 0;
        padding-right: 3%; }


      ul.navigation {
        display: inline-block;
        float: right;
        li {
          margin: 0 rv(-5);
          white-space: nowrap;
          &.active {
            a {
              color: $clr2; } }
          &:last-child {
            display: none !important; }
          a {
             padding-right: rv(-1);
             background-position-x: right; }
          a:hover {
            background: url("../../asset/img/svg/icons/menu_dot.svg") no-repeat;
            background-position-x: right;
            background-size: rv(-2);
            background-position-y: center; } } } }
    a {
      color: $white;
      font-weight: $bold;
      font-family: BentonSans;
      span {
        margin: 0 auto; } }
    a:hover {
      color: $clr2;
      text-decoration: none; }

    .down-arrow-black {
      vertical-align: top;
      display: inline-block;
      width: rv(1);
      height: rv(1);
      background: url("../../asset/img/svg/icons/hamburger_menu.svg") no-repeat;
      @include bp($stack) {
        vertical-align: initial; } }
    ul {
      li {
        display: inline; } } }
  > footer {
    border-top-right-radius: $borderRadius;
    color: $white;
    background-color: $black;
    padding: rv(1);
    display: grid;
    grid-template-columns: 1fr 1fr;
    @include bp($stack) {
      display: grid;
      grid-template-rows: initial;
      grid-template-columns: 15% 1fr 1fr 15%;
      grid-row-gap: initial;
      padding: rv(-1);
      @include fluid_type(-1,0);
      line-height: rv(-1);
      grid-column-gap: rv(-2); }
    @include bp($tablet) {
      grid-column-gap: rv(2);
      padding: rv(2) rv(4); }

    div {
      margin: auto;
      @include bp($stack) {
        grid-column: auto;
        margin-top: 0; } }
    .footer--text {
      p {
        margin-top: rv(-1);
        padding: 0;
        margin-top: 0;
        @include bp($stack) {
          padding: 0; }
        @include bp($tablet) {
          padding: 0 rv(1);
          line-height: rv(2); } } }
    .footer--logo {
      margin: 0;
      grid-column: 2 / 2;
      margin-top: rv(1);
      @include bp($stack) {
        grid-column: auto;
        align-self: flex-end; }
      img {
        width: rv(8); } }
    .footer--recountlogo {
      margin: auto;
      img {
        width: 80%;
        @include bp($stack) {
          width: rv(10); } } }
    nav {
      grid-column: 2 /2;
      width: 100%;
      grid-template-columns: unset;
      display: grid;
      @include bp($stack) {
        grid-column: 3/ 3;
        grid-template-columns: 1fr 1fr 1fr; }
      ul {
        padding-left: 0;
        line-height: font-value(2);
        margin-top: unset;
        @include bp($stack) {
          margin-top: 0; }
        li {
          list-style-type: none;
          &:first-child {
            margin-bottom: rv(-3);
            list-style-type: none; }

          a {
            color: $white;
            text-decoration: underline; } } } } } }


.site-guide-cont {
  @include negative-content-margin(6);
  display: grid;
  align-items: center;
  grid-column-gap: rv(-1);
  grid-template-rows: none;
  grid-row-gap: 0;
  grid-template-columns: none;
  @include bp($stack) {
    display: flex;
    grid-template-columns: 3fr 2fr 2fr; }
  > div {
    grid-column: auto;
    padding: 0;
    @include bp($stack) {
      padding: 0 rv(0); } }
  p {
    font-size: font-value(-1);
    margin-left: rv(1); }
  h5 {
    text-decoration: underline;
    margin-bottom: rv(-3);
    display: inline-block; }
  i {
    display: inline-block;
    background: black;
    border-radius: 3px;
    width: 20px;
    height: 20px;
    margin-right: 10px; }
  h1 {
    font-weight: $heavy; } }

.site-guide-head {
  align-self: flex-start;
  text-align: right;
  p {
    font-weight: $semibold;
    margin-left: 0; } }

.access-dropdown {
  color: #fff;
  text-align: left;
  padding-right: 0;
  background: black;
  float: right;
  width: auto;
  position: absolute;
  right: 0;

  ul {
    padding-left: 10px;
    grid-template-columns: unset;
    list-style-type: none;
    li {
      .toggle-label {
        position: relative;
        top: -3px; } } } }

.access-dropdown-mobile {
  color: #fff;
  text-align: right;
  padding-right: 0;
  background: black;
  float: right;
  width: 100%;
  position: absolute;
  @include bp($stack) {
    display: none; }
  ul {
    padding-left: 10px;
    grid-template-columns: unset;
    list-style-type: none; }
  li {
    .toggle-label {
      position: relative;
      top: -3px; } } }

.dark-switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 24px; }

.dark-switch input {
  opacity: 0;
  width: 0;
  height: 0; }

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s; }

.slider:before {
  position: absolute;
  content: "";
  height: 16px;
  width: 16px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s; }

input:checked + .slider {
  background-color: $clr1; }

input:focus + .slider {
  box-shadow: 0 0 1px $clr1; }

input:checked + .slider:before {
  -webkit-transform: translateX(16px);
  -ms-transform: translateX(16px);
  transform: translateX(16px); }

.slider.round {
  border-radius: 34px; }

.slider.round:before {
  border-radius: 50%; }
.profile-overlay-btn {
  display: none; }
.profile-overlay-logo {
  display: none!important; }

.preview-block {
  a {
    display: none; }
  .resource-list {
    .item {
      &::after {
        content: unset; } } }
  .resource-list {
    grid-template-columns: 1fr;
    @include bp($stack) {
      grid-template-columns: 1fr 1fr 1fr; }

    &.preview {
      a {
        display: block; } } } }

.deck {
  font-family: $codefont; }

section {
  main {
    > div {
      > .preview-block {
        margin-top: 0!important; } } } }
