#homepage-splash {
  .introductory-text {
    &::after {
      content: " ";
      background: url("../../asset/img/svg/marks/icon_trans_white_mark.svg") no-repeat;
      width: 5rem;
      height: 5rem;
      display: flex;
      position: relative;
      align-self: flex-end;
      margin-left: 15px;
      @include bp($stack) {
        background: url("../../asset/img/svg/marks/icon_trans_darK_mark.svg") no-repeat;
        width: 6rem;
        height: 6rem;
        bottom: rv(0);
        left: rv(1);
        margin-left: unset;
        margin-top: rv(-1); } } }


  .slick-prev-splash {
    position: absolute;
    width: rv(5);
    height: rv(5);
    top: 49%;
    transform: translateY(-50%);
    left: 20px;
    z-index: 9999;
    cursor: pointer;
    display: none;
    background: url("../../asset/img/svg/icons/left_arrow_white.svg") no-repeat;
    &:hover {
      background: url("../../asset/img/svg/icons/left_arrow_white_active.svg") no-repeat; }
    @include bp($stack) {
      display: block; } }

  .slick-next-splash {
    position: absolute;
    width: rv(5);
    height: rv(5);
    top: 49%;
    transform: translateY(-50%);
    right: 15px;
    z-index: 9999;
    cursor: pointer;
    display: none;
    background: url("../../asset/img/svg/icons/right_arrow_white.svg") no-repeat;
    &:hover {
      background: url("../../asset/img/svg/icons/right_arrow_white_active.svg") no-repeat; }
    @include bp($stack) {
      display: block; } }

  .slick-dots {
    width: 67%;
    @include bp($stack) {
      width: 60%; } }
  .slick-slide {
    @include bp($stack) {
      height: 100%; } }

  figure {
    height: rv(8)!important;
    max-height: initial!important;

    a {
      display: block;
      height: 100%; }

    img {
      object-fit: cover;
      height: 100%; }

    @include bp($stack) {
      height: 100%!important;
      max-height: initial!important; } }

  margin-left: 0;
  margin-right: 0;
  display: flex;
  flex-direction: column-reverse;
  justify-content: flex-start;
  width: 100%;
  overflow-y: hidden;
  position: relative;

  @include bp($stack) {
    flex-direction: row;
    justify-content: flex-start;
    grid-template-columns: 1fr 70%;
    display: grid;

    &:after {
      position: absolute;
      content: "";
      height: 100%;
      width: 100%;
      top: 0;
      left: 0;
      background: $home_rtl_dark_gradient; } }

  .items {
    position: relative;
    width: 100%;
    right: 0;
    top: -20px;

    @include bp($stack) {
      position: absolute;
      height: 100%;
      width: 70%;
      top: unset; } }
  .item {
    height: 100%; }

  .flame {
    height: 100%;
    z-index: 1;
    display: none;
    @include bp($stack) {
      display: block; }
    img {
      max-width: none;
      width: auto;
      height: 100%; } }
  .resource.introduction {
    @include bp($stack) {
      width: 100%;
      margin-left: -3px;
      height: 100%; }
    z-index: 10;

    .introductory-text {
      height: 100%;
      margin-right: 0;
      margin-left: 0;
      padding: rv(2);
      padding-top: 0;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      background-color: $clr2;
      z-index: 2;
      flex: 1;
      font-weight: $bold;
      border-right: $borderWidth solid $white;
      border-bottom-right-radius: $borderRadius;
      border-bottom: $borderWidth solid #fff;
      border-left: $borderWidth solid #fff;

      @include bp($stack) {
        padding-top: rv(0);
        padding-bottom: rv(0);
        font-weight: $normal;
        border-bottom: 0;
        border-left: 0; } } }

  .scroll-down {
    display: none;

    @include bp($stack) {
      display: block; } }

  .down-arrow {
    color: $black;

    span {
      color: $black; }

    &:hover {
      color: set-colour(1);

      span {
        color: set-colour(1); } } }

  .warning-lug {
    width: 100%;
    height: fit-content;
    background-color: $black;
    opacity: 0.8;
    color: $white;
    border-top: 2px solid $white;
    padding: 1.5rem; //@todo replace with mobile edge factor
    @include font-size(-1);
    display: flex;
    flex-direction: column;
    justify-content: center;
    z-index: 1;

    @include bp($stack) {
      position: absolute;
      width: rv(8);
      bottom: rv(1);
      left: rv(1);
      padding: rv(-1);
      border: 1px solid $white; }

    p {
      font-size: 1em; }

    h4 {
      font-family: $basefont;
      font-size: 1.5em;
      color: $white;
      text-transform: uppercase;
      text-decoration: underline;
      margin-top: 0px;
      margin-bottom: rv(-2);
      display: flex;
      flex-direction: row;
      align-items: center;

      img {
        padding-right: 4px; } } } }

#huronia-splash-logo {
  margin-bottom: rv(0); }
