.section-intro-splash {
  margin-left: 0;
  margin-right: 0;
  width: 100%;
  max-width: 100%;
  position: relative;
  display: flex;
  flex-direction: column-reverse;
  overflow: hidden;

  @include bp($stack) {
    background: $black; }

  figure {
    position: relative;

    a {
      display: block;
      height: 100%;
      width: 100%;
      background: $clr5;
      img {
        filter: grayscale(100%);
        mix-blend-mode: multiply; } }

    &:after {
      position: absolute;
      content: "";
      height: 100%;
      width: 100%;
      top: 0;
      left: 0;
      opacity: 1;

      @include bp($stack) {
 } } }       // background: $ltr_dark_gradient


  .resource {
    @include bp($stack) {
      height: 100%;
      width: 67%;
      flex: 1;
      display: flex;
      figure {
        height: 100%;
        width: 100%;

        a {
          display: block;
          height: 100%; } }

      img {
        display: block;
        background-position: center bottom;
        object-fit: cover;
        height: 100%; } } }


  .introduction {
    @include splash_area_title_right();


    .introductory-text {
      text-align: left;
      position: relative;
      margin: 0;

      h1 {
        margin-bottom: rv(0); }

      p {

        display: block;
        text-align: left;
        line-height: 1.8;

        @include bp($stack) {
          font-size: +font-value(-1);
          line-height: 1.5; }

        @include bp($desktop) {
          font-size: +font-value(0);
          line-height: 1.8; } }

      h1, p, a {
        @include bp($stack) {
          color: $white; } }

      a {
        color: $clr1; } } }



  &.left {
    &:after {
      background: $rtl_dark_gradient; }

    .introduction {
      @include splash_area_title_left();

      .introductory-text, p {
        text-align: left; } } } }
